					</view>
				</view>
				
				<view class="goods-info clear">
					<view class="title">
						<view class="goods-name">
							{{ products.title }}
						</view>
						<view class="iconfont goods-share" @click="onShare">
							<view>&#xe661;</view>
							<view>分享</view>
						</view>
					</view>
					<view class="goods-info-box">
						<text>库存: {{ products.store_nums }}件</text>
						<text>销量: {{ products.sale }}件</text>
					</view>
				</view>
				
				<view class="add-wrap clear" v-if="group.count > 0">
					<view class="title">{{group.count}}人在拼单，可直接参与</view>
					<view class="add-list clear">
						<swiper
						 :autoplay="true" 
						 :circular="true" 
						 :vertical="true"
						>
							<swiper-item 
