											></count-down>
										</view>
									</view>
									<view class="btn">
										<text @click="goCart(value.id)">去拼单</text>
									</view>
								</view>
							</swiper-item>
						</swiper>
					</view>
				</view>
				
				<view class="group-wrap clear">
					<view class="title">
						<view>拼团玩法</view>
					</view>
					<view class="group-box">
						<view class="line"></view>
						<view class="item">
							<text>1</text>
							<text>开团/参团</text>
						</view>
						<view class="item">
							<text>2</text>
							<text>邀请好友</text>
						</view>
						<view class="item">
							<text>3</text>
							<text>满员发货</text>
						</view>
					</view>
				</view>
				
				<view class="goods-comments clear">
