						<text v-if="comments.length > 0" @click="$utils.navigateTo('comments/view',{ id: products.goods_id,type: 'group' })">更多 &gt;</text>
					</view>
					<view class="comments-empty" v-if="comments.length <= 0">该商品还没有评论哦！</view>
					<view
