				</view>
			</view>
			
		</mescroll-body>
		
		<sku-action
			v-model="isSkuStatus"
			:goods="products"
			:attribute="attribute"
			:item="item"
			:goods-info.sync="selectedGoodsInfo"
			:fields="fields"
		></sku-action>
		
		<share
			v-model="isShareStatus"
			:title="products.title"
			:url="url"
			:text="products.title"
			:image="products.photo"
			@onshare="onShareEvent"
		></share>
		
