			<goods-action-icon icon="home" @click="$utils.switchTab('index/index')" text="首页"></goods-action-icon>
			<view class="goods-action-button button-cart" @click="onAddCartClicked">单独购买</view>
			<view class="goods-action-button button-buy" @click="onBuyClicked">发起拼单</view>
		</view>
		
		<!-- #ifdef H5 -->
		<view class="share-weixin" v-if="isWechat && $utils.platformAgent().isWechat" @click="isWechat=false"></view>
		<!-- #endif -->
