			v-if="path==''"
			isRenderImage
			:board="painter"
			@success="path = $event"
			/>
			<image v-if="path !=''" :src="path" style="width: 650rpx;height: 950rpx;"></image>
			
			<!-- #ifndef H5 -->
			<view @click="savePoster" class="savePoster">保存并分享</view>
			<!-- #endif -->
			<view class="iconfont poster-close" @click="posterClose">&#xe600;</view>
		</view>
		<loading v-if="isShowPoster" color="rgba(0,0,0,.5)" :layer="true" :isShowLoading="false"></loading>
		
		<loading v-if="isLoading" color="rgba(255,255,255,1)" :layer="true"></loading>
		
