<script>
import MescrollMixin from "@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js";
import SkuAction from '../../components/sku-action/sku-action';
import GoodsAction from "../../components/goods-action/goods-action";
import GoodsActionButton from "../../components/goods-action/goods-action-button";
import GoodsActionIcon from "../../components/goods-action/goods-action-icon"
import share from "../../components/share/index.vue";
import loading from '../../components/tool/loading'
import navbar from "@/components/navbar/navbar";
import lPainter from '@/uni_modules/lime-painter/components/lime-painter/'
import poster from '@/common/poster';
// #ifdef H5
import { setShareData,shareConfig } from "../../common/wechat";
// #endif
import authorize from "@/components/authorize/authorize";

export default {
	mixins: [MescrollMixin],
    components: {
        SkuAction,
        GoodsAction,
        GoodsActionButton,
        GoodsActionIcon,authorize,
		share,loading,navbar,lPainter
    },
    data() {
        return {
			isAuthShow: false,
			isWechat: false,
			path: "",
			isShowPoster: false,
			isCreatePoster: false,
			scrollNum: 0,
			isLoading:true,
			upOption: {
				use: false,
				auto: false
			},
            fields:["id","goods_id"],
			images: [],
			isSkuStatus: false,
			isShareStatus: false,
			cartCount: 0,
			selectedGoodsInfo: {},
			products: {},
			attribute: [],
			item:{},
			current: 0,
			kId: 0,
			group:{},
			isActivityStatus: false,
			comments: [],
			goods_id:0,
			url: "",
			painter: {
				width: '650rpx',
				height: '950rpx',
				views: []
			}
        };
    },
	onLoad(options) {
		// #ifdef H5
		shareConfig("group");
		// #endif
		
		// #ifdef MP-WEIXIN
		this.$spread.setSpreadUsers(options);
		// #endif
		
		this.goods_id = options.id;
	},
    onShow() {
        //this.loadGoodsData();
    },
	onPageScroll(obj){
		this.scrollNum = obj.scrollTop;
	},
    methods: {
		onShare(){
			this.isShareStatus = !this.isShareStatus;
		},
		posterClose(){
			this.isShowPoster = false;
		},
		savePoster(){
			this.isShowPoster = false;
			uni.saveImageToPhotosAlbum({
				filePath: this.path,
				success(res) {
					uni.showToast({
						title: '已保存到相册',
						icon: 'success',
						duration: 2000
					})
				}
			})
		},
		onShareEvent(event){
			switch(event.id){
				case "poster":
					this.isShowPoster = true;
					this.isShareStatus = false;
					break;
				case "wechat":
					this.isWechat = true;
					this.isShareStatus = false;
					break;
			}
		},
		onSwiperChange(event){
			this.current = event.detail.current;
		},
		downCallback(){
			setTimeout(()=>{
				this.loadGoodsData();
				this.mescroll.endSuccess(10, false);
			},1200);
		},
		triggerDownScroll(){
			this.mescroll.triggerDownScroll();
		},
        loadGoodsData(){
            this.$http.getGroupDetail({
                id: this.goods_id
            }).then((result)=>{
				this.products = result.data.goods;
				this.attribute = result.data.attr;
				this.item = result.data.item;
				this.images = result.data.photo;
				this.group = result.data.group;
				this.comments = result.data.comments;
				this.url = this.$config.uni_app_web_url + "pages/group/view?id=" + this.goods_id;
				this.isLoading = false;
				this.painter.views = poster.config({
					goods_id:     this.goods_id,
					goods_type:   1,
					goods_name:   this.products.title,
					goods_image:  this.images[0] || this.products.photo,
					sell_price:   this.products.sell_price,
					market_price: this.products.market_price,
					url:          "pages/group/view?id=" + this.goods_id
				});
				// #ifdef H5
				setShareData({
					title: this.products.title,
				    imgUrl: this.images[0]
				});
				// #endif
            }).catch(err=>{
                //console.log(err);
            });
        },
        onChange(index) {
            this.current = index;
        },
        onBuyClicked(){
            if(this.isActivityStatus == false){
				this.$utils.msg("活动己结束！");
				return false;
			}

			if(this.isSkuStatus == false){
				this.isSkuStatus = true;
				return ;
			}

			if(!this.selectedGoodsInfo.isSubmit){
				this.$utils.msg("请选择规格！");
				return false;
			}

			this.$store.dispatch("usersStatus").then(()=>{
				this.$utils.navigateTo("cart/confirm",{
					id: this.selectedGoodsInfo.id,
					kid: this.kId,
					sku_id: this.selectedGoodsInfo.selectedSku.id,
					num: this.selectedGoodsInfo.num,
					type: "group"
				});
			}).catch(()=>{
				this.isAuthShow = true;
			});
        },
        onAddCartClicked(){
            if(this.isSkuStatus == false){
				this.isSkuStatus = true;
				return ;
			}

			if(!this.selectedGoodsInfo.isSubmit){
				this.$utils.msg("请选择规格！");
				return false;
			}

			this.$store.dispatch("usersStatus").then(()=>{
				this.$utils.navigateTo("cart/confirm",{
					id: this.selectedGoodsInfo.goods_id,
					sku_id: this.selectedGoodsInfo.selectedSku.id,
					num: this.selectedGoodsInfo.num,
					type: "buy"
				});
			}).catch(()=>{
				this.isAuthShow = true;
			});
        },
		goCart(id){
			this.kId = id;
			this.isSkuStatus = true;
		}
    },
	watch:{
		isShow(value){
			if(value === false){
				this.kId = 0;
			}
		}
	},
	// #ifdef MP-WEIXIN
	onShareAppMessage: function () {
	    let desc = "";
	    let path = "/pages/group/view?id=" + this.goods_id;
		let users = this.$storage.getJson("users");
	    if(users != null){
	      path += `&u=${users.id}`
	      desc = users.nickname + "，为您推荐";
	    }
	    
	    return {
	      title : this.products.title,
	      desc  : desc,
	      path  : path
	    };
	},
	onShareTimeline(){
	    let query = "id=" + this.data.goods_id;
	    let users = this.$storage.getJson("users");
	    if(users != null){
	      query += `&u=${users.id}`;
	    }
	    
	    return {
	      title   : this.$config.web_name,
	      query   : query,
	      imageUrl: ""
	    };
	}
	// #endif
}
</script>

